.contacts {
  padding-left: 30px;
  padding-right: 63px;
  padding-top: 53px;
  padding-bottom: 70px;
  max-width: 683px;
  width: percentage(calc(683 / 1366));
  display: flex;
  flex-direction: column;

  @include vp-1199 {
    padding-top: 63px;
    padding-left: 133px;
    padding-right: 179px;
    max-width: none;
    width: 100%;
  }

  @include vp-767 {
    padding: 0 15px;
    padding-top: 67px;
  }
}

.contacts h2 {
  margin: 0;
  margin-left: 90px;
  margin-bottom: 42px;
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;

  @include vp-1199 {
    font-size: 42px;
    line-height: 44px;
    margin: 0 auto;
    margin-bottom: 40px;
    width: 456px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 34px;
    width: 100%;
  }
}

.contacts__list {
  position: relative;
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: minmax(max-content, 230px) minmax(max-content, 175px);
  column-gap: 115px;
  row-gap: 25px;
  max-width: 405px;
  margin-left: auto;
  margin-right: auto;

  @include vp-1199 {
    column-gap: 96px;
    row-gap: 30px;
    margin: 0 auto;
    max-width: none;
  }

  @include vp-767 {
    grid-template-columns: auto;
    margin: 0;
    row-gap: 29px;
  }
}

.contacts__list::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 1px;
  background-color: #74819c;
  top: 0;
  left: 57%;
}

.contacts__item:first-of-type {
  position: relative;
}

