.contacts__item h3 {
  margin: 0;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 7px;

  @include vp-1199 {
    font-size: 20px;
    line-height: 22px;
    margin-bottom: 11px;
  }
}

.contacts__item p,
.contacts__item a {
  font-size: 14px;
  line-height: 16px;

  @include vp-1199 {
    font-size: 18px;
    line-height: 20px;
  }
}

.contacts__item p {
  margin: 0;
}

.contacts__item a {
  color: $color-sapphire;
  display: block;
  width: max-content;
}

.contacts__item a:hover,
.contacts__item a:focus {
  text-decoration: underline;
}

.contacts__item a:focus {
  outline: 1px solid #102654;
  outline-offset: 3px;
}

.contacts__item a:active {
  opacity: 0.7;
  text-decoration: none;
  outline: none;
}

.contacts__item {
  @include vp-767 {
    order: 1;
  }
}

.contacts__item--address {
  @include vp-767 {
    order: -1;
  }
}

.contacts__item--time {
  @include vp-767 {
    order: 0;
  }
}
